header {
    min-height: 1020px;
    position: relative;
}

.header__bg {
    min-height: 1020px;
    display: flex;
    justify-content: space-between;
    position: absolute;
    z-index: -1;
    max-height: 1020px;

    &--left {
        width: 47%;
    }

    &--right {
        width: 63%;
    }
}

.top-menu {
    padding-top: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-weight: 700;
    font-size: 16px;
    line-height: 18px;

    &__left-menu {
        display: flex;
        gap: 20px;
        list-style-type: none;
    }

    &__left-item--logo {
        position: relative;
        top: -4px;

        font-weight: 700;
        font-size: 25px;
        line-height: 84.99%;
        letter-spacing: -3px;
        color: $orange;
        margin-left: 60px;
        &::before{
            content: "";
            background-image: url(../../../img/sprites/logo.svg);
            background-repeat: no-repeat;
            height: 50px;
            width: 50px;
            position: absolute;
            padding-right: 50px;
            left: -60px;
            top: -10px;
        }

    }

    &__left-item {
        &:hover {
            color: $orange;
        }
    }

    &__right {
        display: flex;
        gap: 20px;
        position: relative;
        margin-right: 75px;


    }

    &__right-btn-mail {
        color: $black;
    }

    &__right-btn-phone {
        color: $white;
    }

    &__right-btn-basket {
        width: 45px;
        height: 45px;
        background-image: url(../../../img/sprites/basket.svg);
        background-repeat: no-repeat;
        position: absolute;
        top: 4px;
        right: -80px;
    }
}

.container {
    max-width: 1960px;
    margin: 0 auto;
}

// пока верстаю сразу под 2560
.wrap-header {
    display: flex;
    justify-content: space-between;
}

.header {

    color: #333333;

    &__left {
        margin-top: 45px;
    }

    &__left-title {
        font-style: normal;
        font-weight: 700;
        font-size: 120px;
        line-height: 100%;
        letter-spacing: -5px;
        color: $white;
        margin-top: 168px;
    }


    &__left-text {
        font-size: 25px;
        line-height: 135%;
        letter-spacing: -1px;
        color: $black;

        font-weight: 700;

        color: #303030;
    }

    &__left-btn-wrap {
        display: flex;
        margin-top: 45px;
        gap: 20px;

    }

    &__left-btn-call {}

    &__left-btn-calc {
        
        
    }

    &__right {

        &>ol {
            list-style: none;
            font-size: 16px;
            padding: 0;
            margin: 10px 0;
        }
    }
}

.huinia {
    margin-right: 500px;
    margin-top: 126px;
    max-width: 318px;
    display: flex;
    flex-direction: column;
    gap: 36px;

    font-weight: 700;
    font-size: 18px;
    line-height: 135%;
    color: $white;

}

ol {
    list-style-type: none;
    counter-reset: num;
    margin: 0 0 0 45px;
    padding: 15px 0 5px 0;
    font-size: 16px;
}

ol li {
    position: relative;
    margin: 0 0 0 0;
    padding: 0 0 10px 0;
    line-height: 1.4;
}

ol li:before {
    content: counter(num);
    counter-increment: num;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: -65px;
    width: 45px;
    height: 45px;
    background: $orange;
    color: $white;
    text-align: center;
    line-height: 25px;
    font-weight: 700;
    font-size: 20px;
    border-radius: 50%;
}

.planka {
    position: absolute;
    height: 100px;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: $white;
    border-radius: 50px 50px 0 0;
}


@media (max-width: 1920px) {
    .container{
        max-width: 1760px;
    }
    .header{
        &__left-title{
            font-size: 105px;
            line-height: 84.99%;
            letter-spacing: -7px;
            font-weight: 500;
        }
    }
    .header__bg{
        &--left {
            width: 53%;
        }
        &--right {
            width: 47%;
        }
    }
}

@media (max-width: 1600px) {
    .container{
        max-width: 1200px;
    }
    .header__bg{
        &--left {
            width: 53%;
        }
        &--right {
            width: 47%;
        }
    }
    .top-menu{
        &__left-menu{
            gap: 10px;
        }
        &__left-item--logo {
            margin-left: -90px;
        }
        &__right{
            margin-right: -60px;
        }
    }
}

@media (max-width: 1440px) {
    .top-menu{
        
        &__left-item--logo {
            margin-left: -10px;
        }
        &__right {
            margin-right: 10px;
        }
    }
    .header{
        &__right{
            &>ol{
                margin: 70px 150px;
            }
        }
    }
}

